import { _decorator, Component, instantiate, Node, input, Input, EventKeyboard, KeyCode, Vec3, Collider2D, Contact2DType, IPhysics2DContact, EPhysics2DDrawFlags, PhysicsSystem2D, Animation, Prefab, macro } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Plane')
export class Plane extends Component {
    static instance: Plane;

    @property(Node)
    public Plane: Node = null;
    @property(Animation)
    public planeAnim: Animation = null;
    @property(Prefab)
    public listBullet: Prefab [] = [];

    moveLeft: boolean = false;
    moveRight: boolean = false;
    moveUp: boolean = false;
    moveDown: boolean = false;
    public moveEnable: boolean = true;
    public spawnEnabled: boolean = true;

    public moveSpeed: number = 200;

    public listItemB = [];
    public listItemR = [1];

    protected onLoad(): void {
        Plane.instance = this;
        // PhysicsSystem2D.instance.enable = true;
        // PhysicsSystem2D.instance.debugDrawFlags  = EPhysics2DDrawFlags.Aabb |
        // EPhysics2DDrawFlags.Pair |
        // EPhysics2DDrawFlags.CenterOfMass |
        // EPhysics2DDrawFlags.Joint |
        // EPhysics2DDrawFlags.Shape;
        input.on(Input.EventType.KEY_DOWN, this.onKeyDown, this);
        input.on(Input.EventType.KEY_UP, this.onKeyUp, this);
    }
    protected onDestroy(): void {
        input.off(Input.EventType.KEY_DOWN, this.onKeyDown, this);
        input.off(Input.EventType.KEY_UP, this.onKeyUp, this);
    }
    protected start(): void {
        let collider = this.getComponent(Collider2D);
        if(collider){
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
        }
    }
    protected update(dt: number): void {
        if(this.moveEnable === false) return;
        this.planeMove(dt);
        this.schedule(this.spawnBullet, 0.5, macro.REPEAT_FOREVER, 0);
        // this.spawnBullet();
    }
    onKeyDown(event: EventKeyboard){
        if(!this.moveEnable) return;
        switch(event.keyCode){
            case KeyCode.KEY_A: this.moveLeft = true; break;
            case KeyCode.KEY_D: this.moveRight = true; break;
            case KeyCode.KEY_W: this.moveUp = true; break;
            case KeyCode.KEY_S: this.moveDown = true; break;

            case KeyCode.ARROW_LEFT: this.moveLeft = true; break;
            case KeyCode.ARROW_RIGHT: this.moveRight = true; break;
            case KeyCode.ARROW_UP: this.moveUp = true; break;
            case KeyCode.ARROW_DOWN: this.moveDown = true; break;

        }
    }
    onKeyUp(event: EventKeyboard){
        if(!this.moveEnable) return;
        switch(event.keyCode){
            case KeyCode.KEY_A: this.moveLeft = false; break;
            case KeyCode.KEY_D: this.moveRight = false; break;
            case KeyCode.KEY_W: this.moveUp = false; break;
            case KeyCode.KEY_S: this.moveDown = false; break;

            case KeyCode.ARROW_LEFT: this.moveLeft = false; break;
            case KeyCode.ARROW_RIGHT: this.moveRight = false; break;
            case KeyCode.ARROW_UP: this.moveUp = false; break;
            case KeyCode.ARROW_DOWN: this.moveDown = false; break;

        }
    }  
    planeMove(dt: number){
        let posX = this.Plane.position.x;
        let posY = this.Plane.position.y;

        if(this.moveLeft){
            if(posX > -575){
                posX -= this.moveSpeed * dt;
            }
            let posPlane = new Vec3(posX, posY);
            this.Plane.setPosition(posPlane);
        }
        if(this.moveRight){
            if(posX < 575){
                posX += this.moveSpeed * dt;
            }
            let posPlane = new Vec3(posX, posY);
            this.Plane.setPosition(posPlane);
        }
        if(this.moveUp){
            if(posY < 340){
                posY += this.moveSpeed * dt;
            }
            let posPlane = new Vec3(posX, posY);
            this.Plane.setPosition(posPlane); 
        }
        if(this.moveDown){
            if(posY > -340){
                posY -= this.moveSpeed * dt;
            }
            let posPlane = new Vec3(posX, posY);
            this.Plane.setPosition(posPlane); 
        }
    }
    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){
        let other = otherCollider.node;
        let self = selfCollider.node;
    }
    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){

    }
    playAnim(countDown: number){
        this.planeAnim.play();
        setTimeout(() => {
            this.planeAnim.stop();
        }, countDown * 1000);
    }
    spawnBullet(){
        if(!this.spawnEnabled) return;
            if(this.listItemB.length === 0){
                let bulletPrefab0 = this.listBullet[0];
    
                let prefabInstance0 = instantiate(bulletPrefab0);
                prefabInstance0.parent = this.node.parent;
                prefabInstance0.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y+60, this.node.worldPosition.z);
            }
            if(this.listItemB.length === 1){
                let bulletPrefab1 = this.listBullet[1];
                let prefabInstance1 = instantiate(bulletPrefab1);
                prefabInstance1.parent = this.node.parent;
                prefabInstance1.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y+60, this.node.worldPosition.z);

                let bulletPrefab2 = this.listBullet[2];
                let prefabInstance2 = instantiate(bulletPrefab2);
                prefabInstance2.parent = this.node.parent;
                prefabInstance2.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y+60, this.node.worldPosition.z);
            }
            if(this.listItemB.length === 2){
                let bulletPrefab3 = this.listBullet[3];
                let prefabInstance3 = instantiate(bulletPrefab3);
                prefabInstance3.parent = this.node.parent;
                prefabInstance3.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y+60, this.node.worldPosition.z);

                let bulletPrefab4 = this.listBullet[4];
                let prefabInstance4 = instantiate(bulletPrefab4);
                prefabInstance4.parent = this.node.parent;
                prefabInstance4.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y+60, this.node.worldPosition.z);
            }
            if(this.listItemR.length === 1){
                let bulletPrefab5 = this.listBullet[5];
                let prefabInstance5 = instantiate(bulletPrefab5);
                prefabInstance5.parent = this.node.parent;
                prefabInstance5.setWorldPosition(this.node.worldPosition.x+60, this.node.worldPosition.y, this.node.worldPosition.z);
                let prefabInstance6 = instantiate(bulletPrefab5);
                prefabInstance6.parent = this.node.parent;
                prefabInstance6.setWorldPosition(this.node.worldPosition.x-60, this.node.worldPosition.y, this.node.worldPosition.z);
        }
    }

}
